.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.shuffle {
  transition:  3s;
  /* position: absolute; */
  left: random(500);
  top: random(500);
}

